import { THours,} from "@/types";

export interface IScheduleProps {
    id: number;
    __component: string;
    heading: string;
    subHeading: string;
    hours: THours[];
};

export function Schedule({data}: {data: IScheduleProps} ) {
    if (!data?.hours) return null;
    
    const { heading, subHeading, hours } = data;
    return (
        <div className="w-full max-w-2xl mx-auto px-8 pb-6">
            <div className="rounded-xl bg-brand-pink border my-4 p-6 shadow-sm transition-shadow hover:shadow-md relative flex flex-col">
                <h2 className="text-xl font-bold text-white mb-4">{heading}</h2>
                <p className="text-center text-white mb-4">{subHeading}</p>
                <div>
                    {hours.map((item: THours) => (
                        <div key={item.id} className="flex justify-between py-2">
                            <div className="font-bold text-white">{item.days}</div>
                            <div className="font-bold text-white">{item.times}</div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    )

}
