import { TContact } from "@/types";
import { MailIcon, MapPinIcon, PhoneIcon } from "lucide-react";

export interface IContactInfoProps {
    id: number;
    __component: string;
    heading: string;
    subHeading: string;
    contact: TContact[];
}

export function ContactInfo({data}: {data: IContactInfoProps} ) {
    if (!data?.contact) return null;
    
    const { heading, subHeading, contact } = data;
    return (
        <div className="w-full max-w-xl mx-auto px-8 py-6 mb-6">
            <div className="rounded-xl border my-4 p-6 shadow-sm transition-shadow hover:shadow-md relative flex flex-col">
                <h2 className="text-xl font-semibold">{heading}</h2>
                <p className="text-lg text-brand-dark-grey mb-2">{subHeading}</p>
                <div className="py-4">
                    {contact.map((item: TContact) => (
                        <div key={item.id} className="mb-4 space-y-3">
                            <div className="flex items-start gap-3">
                                <PhoneIcon className="w-5 h-5 mt-0.5 text-brand-gold shrink-0" />
                                <div>
                                    <h3 className="font-semibold">Phone Number</h3>
                                    <p className="text-gray-600">{item.phone}</p>
                                </div>
                            </div>
                            <div className="flex items-start gap-3">
                                <MailIcon className="w-5 h-5 mt-0.5 text-brand-gold shrink-0" />
                                <div>
                                    <h3 className="font-semibold text-brand-dark-grey">Email</h3>
                                    <p className="text-black">{item.email}</p>
                                </div>
                            </div>
                            <div className="flex items-start gap-3">
                                <MapPinIcon className="w-5 h-5 mt-0.5 text-brand-gold shrink-0" />
                                <div>
                                    <h3 className="font-semibold">Location</h3>
                                    <p className="text-gray-600">{item.location}</p>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    )
}