import { IContactFormLabelsProps, ContactFormLabels } from "@/components/custom/contact-form";
import { IContactInfoProps, ContactInfo } from "@/components/custom/contact-info";
import { IScheduleProps, Schedule } from "@/components/custom/schedule";
import { loaders } from "@/data/loaders";
import { validateApiResponse } from "@/lib/error-handler";

export type TContactPageBlocks = IScheduleProps | IContactInfoProps | IContactFormLabelsProps;

function blockRenderer(block: TContactPageBlocks, index: number) {
    switch (block.__component) {
        case "layout.schedule":
            return <Schedule key={index} data={block as IScheduleProps} />;
        case "layout.contact-info":
            return <ContactInfo key={index} data={block as IContactInfoProps} />;
        case "layout.contact-form":
            return <ContactFormLabels key={index} data={block as IContactFormLabelsProps} />;
        default:
            return null;
    }
}

export default async function ContactPage() {
    const contactPageData  = await loaders.getContactPageData();
    const data = validateApiResponse(contactPageData, "contact page");
    const {blocks} = data;

    return (
        <main className="container mx-auto px-4 py-12">
            <div className="grid grid-cols-1 lg:grid-cols-5 gap-8">
                {/* Left Column - Schedule and Contact Info */}
                <div className="space-y-8 lg:col-span-2">
                    {blocks.map((block, index) => {
                        if (block.__component === "layout.schedule" || block.__component === "layout.contact-info") {
                            return blockRenderer(block, index);
                        }
                    })}
                </div>   
                {/* Right Column - Contact Form */}
                <div className="space-y-8 lg:col-span-3">
                    {blocks.map((block, index) => {
                        if (block.__component === "layout.contact-form") {
                            return blockRenderer(block, index);
                        }
                    })}
                </div>
            </div>
        </main>
    );
}